import { useContext, useEffect, useState } from "react";
import SimulationContext from "./SimulationContext";
import SVG from 'react-inlinesvg';
import uuid from "react-uuid";
import { Table } from "react-bootstrap";

export default function ChipDesign() {
    const { simulationResult } = useContext(SimulationContext);
    const [result, setResult] = useState();
    const [inputs, setInputs] = useState();
    const [error, setError] = useState(false);
    const [errorMsg, setErrorMsg] = useState('');

    useEffect(() => {
        var input = {}
        simulationResult.ast.inputs.map(inp => {
            input[inp] = false;
        });
        setInputs(input)
        try {
            var outputValues = simulationResult.getValues(input, simulationResult.ast);
            var ast = { ...simulationResult.ast };
            ast['inputValues'] = input;
            ast['outputValues'] = outputValues;
            setResult({ ast: ast })
        } catch (error) {
            setError(true);
            setErrorMsg(error);
        }
    }, [])

    const changeInput = (changedKey) => {
        var input = {}
        if (inputs) {
            for (var key in inputs) {
                input[key] = key === changedKey ? !inputs[key] : inputs[key]
            }
            setInputs(input)
            try {
                var outputValues = simulationResult.getValues(input, simulationResult.ast);
                var ast = { ...simulationResult.ast };
                ast['inputValues'] = input;
                ast['outputValues'] = outputValues;
                setResult({ ast: ast })
            } catch (error) {
                setError(true);
                setErrorMsg(error);
            }
        }
    };
    return error ? <div className="svg-container text-warning">{errorMsg}</div> : (result && result.ast ? <Chip error={error} errorMsg={errorMsg} changeInput={changeInput} chip={result.ast} /> : <></>)
}
function Chip(props) {
    var vw = Math.max(document.documentElement.clientWidth || 0, window.innerWidth || 0)
    var vh = Math.max(document.documentElement.clientHeight || 0, window.innerHeight || 0)
    var iconStr;
    var chipHeight;
    var chipWidth = 100;

    useEffect(() => {
        function handleResize() {
            vw = Math.max(document.documentElement.clientWidth || 0, window.innerWidth || 0)
            vh = Math.max(document.documentElement.clientHeight || 0, window.innerHeight || 0)
        }
        window.addEventListener('resize', handleResize);
        return _ => {
            window.removeEventListener('resize', handleResize);
        }
    })

    var inputLines = [];
    var outputLines = [];
    var inputValues = props.chip['inputValues'];
    var outputValues = props.chip.outputValues;
    var maxGroupLength = 0;

    const groupIt = (array) => {
        let resultObj = {};

        for (let i = 0; i < array.length; i++) {
            let currentWord = array[i];
            let firstChar = currentWord[0];
            let innerArr = [];
            if (resultObj[firstChar] === undefined) {
                innerArr.push(currentWord);
                resultObj[firstChar] = innerArr
            } else {
                resultObj[firstChar].push(currentWord)
            }
        }
        return resultObj
    }

    var inputGroups = groupIt(Object.keys(inputValues));
    var outputGroups = groupIt(Object.keys(outputValues));
    var inputValuesGroup = {};
    var outputValuesGroup = {};

    for (var inputGroupKey in inputGroups) {
        var group = inputGroups[inputGroupKey];
        group = group.length > 1 ? group.map(el => parseInt(el.replace(inputGroupKey, ''))) : group;
        group = group.sort();

        var value = 0;
        if (group.length > 1) {
            group.map((el, index) => {
                value = value + (inputValues[inputGroupKey + el] ? 1 : 0) * Math.pow(2, index)
            });
        } else {
            value = inputValues[group[0]] ? 1 : 0;
        }
        maxGroupLength = group.length > maxGroupLength ? group.length : maxGroupLength;

        inputValuesGroup[group.length > 1 ? inputGroupKey : group[0]] = value;
    }

    for (var outputGroupKey in outputGroups) {
        var group = outputGroups[outputGroupKey];
        group = group.length > 1 ? group.map(el => parseInt(el.replace(outputGroupKey, ''))) : group;
        group = group.sort();

        var value = 0;
        if (group.length > 1) {
            group.map((el, index) => {
                value = value + (outputValues[outputGroupKey + el] ? 1 : 0) * Math.pow(2, index)
            });
        } else {
            value = outputValues[group[0]] ? 1 : 0;
        }
        maxGroupLength = group.length > maxGroupLength ? group.length : maxGroupLength;

        outputValuesGroup[group.length > 1 ? outputGroupKey : group[0]] = value;
    }

    var inputLength = 0, outputLength = 0;

    if (!props.error) {
        inputLength = Object.entries(inputValues).length + 1;

        outputLength = Object.entries(outputValues).length + 1;
        chipHeight = (Math.max(inputLength, outputLength) < 5) ? 100 : (Math.max(inputLength, outputLength) * 20);

        var horizontal = false;

        if (Math.max(inputLength, outputLength) > 10) {
            chipHeight = 100;
            chipWidth = (Math.max(inputLength, outputLength) * 20);
            chipWidth = chipWidth < 375 ? 375 : chipWidth;
            horizontal = true;
        }
        var counter = 1;

        for (var key in inputValues) {
            if (horizontal) {
                inputLines.push({ value: inputValues[key], key: key, yPos: 150, xPos: (counter * chipWidth / inputLength) + 5 });
            } else {
                inputLines.push({ value: inputValues[key], key: key, xPos: "-15", yPos: (counter * chipHeight / inputLength) + 15 });
            }

            counter++;
        }

        counter = 1;

        for (var key in outputValues) {
            if (horizontal) {
                outputLines.push({ value: outputValues[key], key: key, yPos: 0, xPos: (counter * chipWidth / outputLength) + 5 });
            } else {
                outputLines.push({ value: outputValues[key], key: key, xPos: "105", yPos: (counter * chipHeight / outputLength) + 15 });
            }
            counter = counter + 1;
        }
    }
    if (props.chip.icon) {
        iconStr = props.chip.icon;
        iconStr = iconStr.replace('$height', chipHeight);
        iconStr = iconStr.replace('$width', chipWidth);
        iconStr = iconStr.replace('$txtPosX', (chipWidth / 2));
        iconStr = iconStr.replace('$txtPosY', (chipHeight / 2));
    } else {
        iconStr = '<svg width="' + chipWidth + '" height="' + chipHeight + '">' +
            '<defs>' +
            '<linearGradient id="grad3" x1="0%" y1="100%" x2="100%" y2="0%">' +
            '<stop offset="10%" style="stop-color:#0c0c0c;stop-opacity:1" />' +
            '<stop offset="90%" style="stop-color:#4D4855;stop-opacity:1" />' +
            '</linearGradient>' +
            '</defs>' +
            '<g><rect width="' + chipWidth + '" height="' + chipHeight + '" style="fill:url(#grad3);fill-opacity=1;stroke-width:1;stroke:rgb(0,0,0)"/>' +
            '<text x="' + (chipWidth / 2) + '" y="' + (chipHeight / 2) + '" font-family="Verdana" font-size="10" fill="cyan" text-anchor="middle">' + props.chip.chip + '</text></g>' +
            '</svg>'
    }

    return <>
        <div className="svg-container" style={{ height: (Math.max(inputLength, outputLength) < 5) ? "60%" : "85%", maxHeight: (Math.max(inputLength, outputLength) < 5) ? "60%" : "98%" }}>
            <svg viewBox={"0 0 " + (chipWidth + 15) + " " + (chipHeight + 15)}>
                <g>
                    <SVG src={iconStr} x='0' y='15' />
                    <text key={uuid()} x={horizontal ? chipWidth / 2 : "115"} y={horizontal ? -20 : 8} fontFamily="Verdana" fontSize="10" fill="#BB86FC" textAnchor="middle">Output</text>
                    <text key={uuid()} x={horizontal ? chipWidth / 2 : "-30"} y={horizontal ? 162 : 8} fontFamily="Verdana" fontSize="10" fill="#03DAC6" textAnchor="middle">Input</text>
                    {
                        inputLines.map(inputLine => {
                            return <g key={uuid()}>
                                <text key={uuid()} x={inputLine.xPos} y={horizontal ? inputLine.yPos : inputLine.yPos - 6} fontFamily="Verdana" fontSize="8" fill="white">{inputLine.key}</text>
                                <line key={uuid()} x1={horizontal ? inputLine.xPos : -30} x2={horizontal ? inputLine.xPos : "0"} y1={horizontal ? 135 : inputLine.yPos} y2={horizontal ? 115 : inputLine.yPos} stroke={inputLine.value ? "green" : "darkred"} strokeWidth="1" />
                                <circle cx={horizontal ? inputLine.xPos : "-30"} className="inputButton" cy={horizontal ? 135 : inputLine.yPos} r="5" stroke="black" strokeWidth="2" fill={inputLine.value ? "darkgreen" : "darkred"} onClick={() => props.changeInput(inputLine.key)} />
                            </g>
                        })
                    }
                    {
                        outputLines.map(outputLine => {
                            return <g key={uuid()}>
                                <text key={uuid()} x={outputLine.xPos} y={outputLine.yPos - 6} fontFamily="Verdana" fontSize="8" fill="white">{outputLine.key}</text>
                                <line key={uuid()} x1={horizontal ? outputLine.xPos : 100} x2={horizontal ? outputLine.xPos : "130"} y1={horizontal ? -2 : outputLine.yPos} y2={horizontal ? 15 : outputLine.yPos} stroke={outputLine.value ? "green" : "red"} strokeWidth="1" />
                                <circle cx={horizontal ? outputLine.xPos : "130"} cy={horizontal ? outputLine.yPos + 2 : outputLine.yPos} r="4" stroke="#fff5be" strokeWidth="1" fill={outputLine.value ? "green" : "red"} >
                                    <animate attributeName="fill" values={outputLine.value ? "#01A368;#32CD32;#01A368" : "#C51E3A;#ED2839;#C51E3A"} dur="3s" repeatCount="indefinite" />
                                </circle>

                            </g>
                        })
                    }
                </g>
            </svg>
        </div>
        {
            maxGroupLength < 8 ? <></> : <Table responsive striped bordered hover size="sm" variant="dark">
                <thead>
                    <tr>
                        {Object.keys(inputValuesGroup).map(el => {
                            return <th key={uuid()}>{el}</th>;
                        })}
                        {Object.keys(outputValuesGroup).map(el => {
                            return <th key={uuid()}>{el}</th>;
                        })}
                    </tr>
                </thead>
                <tbody>
                    <tr >
                        {
                            Object.keys(inputValuesGroup).map(el => {
                                return <td key={uuid()}>{inputValuesGroup[el]}</td>
                            })
                        }
                        {
                            Object.keys(outputValuesGroup).map(el => {
                                return <td key={uuid()}>{outputValuesGroup[el]}</td>
                            })
                        }
                    </tr>

                </tbody>
            </Table>
        }
    </>
}